.container1 {
    font-family: 'Space Grotesk', sans-serif;
    margin: 0 auto;
    padding: 0;
    background-color: #f2f2f200;
    max-width: 1100px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin-top: 0%;
    z-index: 200;
}

[data-theme="dark"] {
    background-color: #282828;
    color: #ffffff;

    .container1 {
        background-color: #28282800;
    }

    .tologoAC {
        border: 2px solid rgb(255, 255, 255);
    }

    .online {
        color: rgb(255, 255, 255);
    }

    .right-item1 {
        color: rgb(255, 255, 255); /* Χρώμα του κειμένου */
        text-shadow: -1px -1px 0 black, 1px -1px 0 black, -1px 1px 0 black, 1px 1px 0 black; /* Δημιουργία περιγράμματος γύρω από τα γράμματα */
    }
}

.tologoAC {
    width: auto;
    height: 70%;
    border-radius: 100%;
    margin: 12px 0 0 20px;
    margin-right: 10px;
    border: 2px solid black;
    
}

.toolocont {
    position: relative;
    width: 100%;
    min-height: 100vh;
}

.left-item1 {
    width: 100%;

    display: flex;
}

.left-item1 p {
    font-size: 20px;
    font-weight: 600;
}

.toonline {
    width: 20%;
    text-align: right;
}

.online {
    height: 70px;
    display: flex;
    color: black;
    font-size: 16px;
    margin-right: 15px;
}

.online p {
    margin-left: 0;
    font-size: 17px;
    margin-top: 27px;
}

.minima {
    margin: 5px 20px;
    background-color: rgb(44, 134, 176);
    color: white;
    display: inline-block;
    padding: 0 15px;
    border-radius: 20px;
}

#mnm5 {
    cursor: pointer;
}

#hr1 {
    text-align: center;
    font-size: 14px;
    color: grey;
}

#tohr {
    width: 90%;
}

.apathsh {
    cursor: pointer;
    float: right;
    margin: 5px 20px;
    color: white;
    text-align: center;
    background-color: rgba(76, 76, 208, 0.4);
    border-radius: 20px;
    padding: 0 15px;
}

.apathsh:focus {
    background-color: rgb(76, 76, 208);
}

.right-item1 {
    font-size: 16px;
    margin: -45px 20px 30px;
    font-weight: 550;
}

#toa {
    color: white;
    text-decoration: none;
    cursor: pointer;
}

#mnm6, #mnm7 {
    border-radius: 0 20px 20px 20px;
}

.hforma {
    width: 100%;
}

.waveContact {
    z-index: 5;
}

#tocopy {
    cursor: pointer;
}

#tostrong {
    color: rgb(17, 15, 107);
}

/* Responsive Styles */
@media (min-width: 601px) and (max-width: 768px) {
    .container1 {
        margin-top: 10%;
        width: 90%;
    }

    .contactdiv {
        width: 95%;
        height: 40px;
    }

    .hforma {
        width: 100%;
        margin-left: -5%;
    }
}

@media (max-width: 600px) {
    .container1 {
        width: 100%;
        margin-left: 0;
        margin-top: 0;
        min-height: 100vh;
    }

    .contactdiv {
        width: 95%;
        height: 25px;
    }

    .hforma {
        width: 90%;
        margin-left: 0;
    }
    
    .left-item1 {
        width: 100%;
        display: flex;
    }
    
    .left-item1 p {
        font-size: 16px;
        font-weight: 600;
    }
    
    .toonline {
        display: none;
    }

}
